import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { Http, Response, RequestOptions, Headers } from "@angular/http";
import { Observable } from "rxjs/Observable";
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, ActivatedRoute, Params } from '@angular/router';
import "rxjs/Rx";
import { CommonService } from "../services/commonService";
import { TimesheetService } from "../services/timesheetService";
import swal from 'sweetalert2';

declare interface DataTable {
    tSheetHeaderRow: string[];
    tSheetFooterRow: string[];
    tSheetDataRows: any;
}

declare const $: any;

@Component({
    selector: 'app-timesheet',
    templateUrl: './timesheet.component.html',
    styleUrls: ['./timesheet.component.scss'],
    providers: [CommonService, TimesheetService]
})
export class TimesheetComponent implements OnInit {
    public tSheetDataTable: DataTable;
    public timesheetTrackList: any;
    public userProfile: any;

    public tSheetTrackrForm = new FormGroup({
        trackChk: new FormControl(),
        checkAll: new FormControl()
    });
    constructor(
        private http: Http,
        private router: Router,
        private commonService: CommonService,
        private timesheetService: TimesheetService,
        private activatedRoute: ActivatedRoute,
    ) { }

    ngOnInit() {
        if (JSON.parse(sessionStorage.getItem('LogEmployee')) != null) {

            this.getWorkOrderDetails();
            this.tSheetDataTable = {
                tSheetHeaderRow: ['#', 'Name', 'Work Order', 'Hours', 'Actions'],
                tSheetFooterRow: ['#', 'Name', 'Work Order', 'Hours', 'Actions'],
                tSheetDataRows: this.timesheetTrackList
            };

            $(document).ready(function () {
                $("#chkAll").click(function () {
                    $('input:checkbox').not(this).prop('checked', this.checked);
                    //$('input:checkbox:not(:checked)').prop('disabled', true);
                });
            });
        }
        else {
            this.router.navigate(['pages/login']);
        }
    }

    selectAll(isChecked) {
        alert(isChecked);
        this.tSheetTrackrForm.controls["trackChk"].value.every(x => x.value == isChecked);
    }

    approvedTimeSheet(status: any) {
        $('.loader').show();
        var chckeValue = [];
        $('input[name="trackChk"]:checked').each(function () {
            chckeValue.push(this.id);
        });
        if (chckeValue.length > 0) {
            this.userProfile = JSON.parse(sessionStorage.getItem('LogEmployee'));

            var contact = {
                ApprovedBy: this.userProfile.ID,
                ApprovalStatus: status
            };

            this.timesheetService.UpdateTimesheetTrack(this.commonService.baseApiUrl, chckeValue, contact).subscribe(
                data => {
                    var datatSheet = JSON.stringify(data);
                    var tSheetParse = JSON.parse(datatSheet);
                    var tSheetList = JSON.parse(tSheetParse["_body"]);
                    if (tSheetList != null) {
                        $('.loader').hide();
                        this.tSheetTrackrForm.reset();
                        this.getWorkOrderDetails();
                    }
                });
        }
        else {
            $('.loader').hide();
        }
    }

    goToViewTimesheet(WorkId: any) {
        this.router.navigate(['view-timesheet'], { queryParams: { woasgtID: WorkId } });
    }

    getWorkOrderDetails() {
        $('.loader').show();
        this.timesheetService.GetTimesheetTrackList(this.commonService.baseApiUrl).subscribe(
            data => {
                var dataWorkOrders = JSON.stringify(data);
                var workParse = JSON.parse(dataWorkOrders);
                var workOrderList = JSON.parse(workParse["_body"]);
                localStorage.removeItem("WorkOrdersList");
                this.timesheetTrackList = null;
                if (workOrderList != null) {
                    $("#tSheetDatatables").dataTable().fnDestroy();
                    localStorage.setItem("WorkOrdersList", JSON.stringify(workOrderList));
                    this.timesheetTrackList = workOrderList;
                    console.log(this.timesheetTrackList);

                    this.tSheetDataTable = {
                        tSheetHeaderRow: ['#', 'Name', 'Work Order', 'Hours', 'Actions'],
                        tSheetFooterRow: ['#', 'Name', 'Work Order', 'Hours', 'Actions'],
                        tSheetDataRows: this.timesheetTrackList
                    };

                    setTimeout(() => {
                        $("#tSheetDatatables").dataTable().fnDestroy();
                        $('#tSheetDatatables').DataTable({
                            'pagingType': 'full_numbers',
                            'lengthMenu': [[10, 25, 50, -1], [10, 25, 50, 'All']],
                            responsive: true,
                            language: {
                                search: '_INPUT_',
                                searchPlaceholder: 'Search records',
                            }

                        });

                        const tableTSheet = $('#tSheetDatatables').DataTable();

                        // Edit record
                        tableTSheet.on('click', '.edit', function () {

                        });

                        // Delete a record
                        tableTSheet.on('click', '.remove', function (e: any) {
                            const $trTT = $(this).closest('tr');
                            tableTSheet.row($trTT).remove().draw();
                            e.preventDefault();
                        });

                        // Like record
                        tableTSheet.on('click', '.like', function () {
                            alert('You clicked on Like button');
                        });

                    }, 10);

                    $('.loader').hide();
                }
            }
        );
    }

}



